<template>
	<div>
		<p class="title">{{list.title}}</p>
		<p class="time">
			<span>{{list.timestamp}}</span>
		<!--	<span style="margin-left: 0.25rem;">18:20</span>-->
			<span style="float: right;">{{list.author}}</span>
		</p>
		<div class="content">
			{{list.content}}
		</div>
		<p class="read">阅读: {{list.read}}</p>
		<Bottom></Bottom>
	</div>
</template>

<script>
	import Bottom from '../../components/Bottom/Bottom'
	export default{
		components:{
			Bottom
		},
		data(){
			return {
				id:this.$route.params.NoticeId,
				list:{}
			}
		},
		mounted(){
			
			this.getData()
		},
		methods:{
			getData(){
				var obj={
					"s":this.$store.state.sessionId,
					"id":this.id
				}
					this.$Api.ArticleDetails(obj).then((res)=>{
						
						this.list=res.article
					})
				
			}
			
		}
	}
</script>

<style scoped="scoped" lang="scss">
@import "../../sass/baseSass.scss";
.title{
	font-size: 0.34rem;
	padding: 0.4rem 0.3rem 0 0.3rem;
	
}
.time{
	margin-top: 0.3rem;
	padding-left: 0.3rem;
	padding-right: 0.3rem;
	box-sizing: border-box;
	font-size: 0.28rem;
	color: #888888;
}
.content{
	font-size: 0.3rem;
	padding: 0.3rem;
}
.read{
	color: #888888;
	padding-left: 0.3rem;
	box-sizing: border-box;
	font-size: 0.28rem;
}
</style>


